import React, { useState, useEffect } from "react";
import "./profile.css";
import { Tabs, Form, Input, Button, Modal } from "antd";
import axiosInstance from "../../../actions/axios";
import store from "../../../store";
import { updateTitle } from "../../common/UpdateTitle";

const { TabPane } = Tabs;

function calculate_age(dob) {
  var diff_ms = Date.now() - dob.getTime();
  var age_dt = new Date(diff_ms);

  return Math.abs(age_dt.getUTCFullYear() - 1970);
}

export default function Profile() {
  const [profileInfo, setProfileInfo] = useState({});
  const [change_password_form] = Form.useForm();
  useEffect(() => {
    const userID = store.getState().auth.user.id;
    axiosInstance.get(`/account/profile/${userID}/`).then((res) => {
      let age = 0;
      if (res.data.profile.birth_date !== null) {
        age = calculate_age(new Date(res.data.profile.birth_date));
      }
      setProfileInfo({ ...res.data, age });
    });
  }, []);
  useEffect(() => {
    updateTitle({
      title:
        `${profileInfo.first_name} ${profileInfo.last_name}` ||
        profileInfo.username,
      metaDescription: "The Meta Description ",
    });
  }, [profileInfo, setProfileInfo]);

  function callback(key) {
    // console.log(key);
  }

  const onFinish = (values) => {
    console.log(values);
  };
  const onChangePassword = (val) => {
    const cbody = JSON.stringify({
      old_password: val.oldpassword,
      new_password: val.password,
    });
    axiosInstance
      .put("/account/change-password/", cbody)
      .then((res) => {
        change_password_form.resetFields();
        Modal.success({
          title: "Success",
          content: <h5>{res.data.message}</h5>,
        });
      })
      .catch((err) => {
        Modal.error({
          title: "Error",
          content: <p>{err.response.data.old_password.join()}</p>,
        });
      });
  };
  return (
    <div className="mdk-header-layout__content page-content pt-32pt pt-sm-64pt">
      <div className="container pt-32pt pt-sm-64pt">
        <div className="main-body">
          <div className="row gutters-sm">
            <div className="col-md-4 mb-3 pt-3">
              <div className="card">
                <div className="card-body">
                  <div className="d-flex flex-column align-items-center text-center">
                    <img
                      src={
                        profileInfo.profile
                          ? profileInfo.profile.avatar
                          : "https://bootdey.com/img/Content/avatar/avatar7.png"
                      }
                      alt={profileInfo.username}
                      className="rounded-circle"
                      width="150"
                      height="150"
                    />
                    <div className="mt-3">
                      <h4>
                        {profileInfo.first_name || "First"} &nbsp;
                        {profileInfo.last_name}
                      </h4>
                      <p className="text-secondary mb-3">
                        {profileInfo.profile ? profileInfo.profile.bio : null}
                      </p>
                      <button className="btn btn-primary mr-1 mb-1">
                        Follow
                      </button>
                      <button className="btn btn-outline-primary mb-1">
                        Message
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-md-8">
              <div className="card mb-3">
                <div className="card-body">
                  <Tabs defaultActiveKey="1" onChange={callback}>
                    <TabPane tab="Details" key="1">
                      <div className="col-xs-12 col-sm-9">
                        <div className="profile-user-info">
                          <div className="profile-info-row">
                            <div className="profile-info-name">Username</div>

                            <div className="profile-info-value">
                              <span>{profileInfo.username}</span>
                            </div>
                          </div>
                          <div className="profile-info-row">
                            <div className="profile-info-name">Email</div>

                            <div className="profile-info-value">
                              <span>{profileInfo.email}</span>
                            </div>
                          </div>

                          <div className="profile-info-row">
                            <div className="profile-info-name">Phone</div>

                            <div className="profile-info-value">
                              <span>
                                {profileInfo.profile
                                  ? profileInfo.profile.phone
                                  : null}
                              </span>
                            </div>
                          </div>

                          <div className="profile-info-row">
                            <div className="profile-info-name">Address</div>

                            <div className="profile-info-value">
                              <i className="fa fa-map-marker light-orange bigger-110"></i>
                              <span>
                                {" "}
                                {profileInfo.profile
                                  ? profileInfo.profile.address
                                  : null}
                              </span>
                            </div>
                          </div>

                          <div className="profile-info-row">
                            <div className="profile-info-name">Age</div>

                            <div className="profile-info-value">
                              <span>{profileInfo.age} years</span>
                            </div>
                          </div>

                          <div className="profile-info-row">
                            <div className="profile-info-name">Institution</div>

                            <div className="profile-info-value">
                              <span>
                                {profileInfo.profile
                                  ? profileInfo.profile.institution
                                  : null}
                              </span>
                            </div>
                          </div>
                          <div className="profile-info-row">
                            <div className="profile-info-name">Occupation</div>

                            <div className="profile-info-value">
                              <span>
                                {profileInfo.profile
                                  ? profileInfo.profile.occupation
                                  : null}
                              </span>
                            </div>
                          </div>

                          <div className="profile-info-row">
                            <div className="profile-info-name">
                              Job Interest
                            </div>

                            <div className="profile-info-value">
                              <span>
                                {profileInfo.profile
                                  ? profileInfo.profile.job_interest
                                  : null}
                              </span>
                            </div>
                          </div>

                          <div className="profile-info-row">
                            <div className="profile-info-name">Gender</div>

                            <div className="profile-info-value">
                              <span>
                                {profileInfo.profile
                                  ? profileInfo.profile.gender
                                  : null}
                              </span>
                            </div>
                          </div>
                        </div>
                      </div>
                    </TabPane>
                    <TabPane tab="Edit" key="2">
                      <Form name="edit" onFinish={onFinish} scrollToFirstError>
                        <Form.Item
                          name="email"
                          label="E-mail"
                          rules={[
                            {
                              type: "email",
                              message: "The input is not valid E-mail!",
                            },
                            {
                              required: true,
                              message: "Please input your E-mail!",
                            },
                          ]}
                        >
                          <Input />
                        </Form.Item>

                        <Form.Item
                          name="nickname"
                          label="Nickname"
                          tooltip="What do you want others to call you?"
                          rules={[
                            {
                              required: true,
                              message: "Please input your nickname!",
                              whitespace: true,
                            },
                          ]}
                        >
                          <Input />
                        </Form.Item>

                        <Form.Item
                          name="phone"
                          label="Phone Number"
                          rules={[
                            {
                              required: true,
                              message: "Please input your phone number!",
                            },
                          ]}
                        >
                          <Input
                            style={{
                              width: "100%",
                            }}
                          />
                        </Form.Item>

                        <Form.Item>
                          <Button
                            type="primary"
                            htmlType="submit"
                            className="btn btn-primary"
                          >
                            Update
                          </Button>
                        </Form.Item>
                      </Form>
                    </TabPane>
                    <TabPane tab="Change Password" key="3">
                      <Form
                        form={change_password_form}
                        name="change"
                        onFinish={onChangePassword}
                        scrollToFirstError
                      >
                        <Form.Item
                          name="oldpassword"
                          label="Old Password"
                          rules={[
                            {
                              required: true,
                              message: "Please input your old password!",
                            },
                          ]}
                        >
                          <Input.Password />
                        </Form.Item>
                        <Form.Item
                          name="password"
                          label="Password"
                          rules={[
                            {
                              required: true,
                              message: "Please input your new password!",
                            },
                          ]}
                          hasFeedback
                        >
                          <Input.Password />
                        </Form.Item>

                        <Form.Item
                          name="confirm"
                          label="Confirm Password"
                          dependencies={["password"]}
                          hasFeedback
                          rules={[
                            {
                              required: true,
                              message: "Please confirm your password!",
                            },
                            ({ getFieldValue }) => ({
                              validator(_, value) {
                                if (
                                  !value ||
                                  getFieldValue("password") === value
                                ) {
                                  return Promise.resolve();
                                }

                                return Promise.reject(
                                  new Error(
                                    "The two passwords that you entered do not match!"
                                  )
                                );
                              },
                            }),
                          ]}
                        >
                          <Input.Password />
                        </Form.Item>

                        <Form.Item>
                          <Button
                            type="primary"
                            htmlType="submit"
                            className="btn btn-danger"
                          >
                            Change Password
                          </Button>
                        </Form.Item>
                      </Form>
                    </TabPane>
                  </Tabs>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
